
.navigation {
  display: none;
}
@media screen and (min-width: 1200px) {
.navigation {
    display: block;
    position: fixed;
    top: 3.5em;
    left: 0;
    list-style: none;
    padding: 0;
    margin: 0;
    z-index: 100;
}
.navigation a {
      display: block;
      position: relative;
      transform: translateX(-100%);
      transition: transform .3s;
      text-decoration: none !important;
}
.navigation a::after {
        content: '';
        top: 0;
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        background: #fff;
        opacity: .35;
        transition: .3s ease-in-out;
}
.navigation a:hover::after {
        opacity: .5;
}
.navigation a.visible {
        transform: translateX(0);
}
.navigation .md-icon {
      margin: .5em;
}
}



