
.content {
  margin: 1.5em auto;
  padding: 1em;
  position: relative;
  z-index: 1;
}
.content::after, .content::before {
    content: '';
    top: 0;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}
.content::before {
    background: #fff;
    opacity: 0;
    transition: .3s ease-in-out;
}
.content::after {
    border: 1px solid #fff;
}
.content:last-child {
    margin-bottom: 0;
}
.content.visible::before {
    opacity: .35;
}
.content.section section:not(:last-child) {
    border-bottom: 1px solid #fff;
}
.content section {
    text-align: left;
}
.content section:last-child p {
      margin-bottom: 0;
}
.content h3 {
    margin-bottom: 0;
}
.content h3 + em {
      margin-top: -.25em;
      display: block;
}
.content h2 {
    margin-top: 0;
}
.content h2 span {
      vertical-align: middle;
      margin-left: .25em;
}
.content ul {
    margin-top: 0;
}
.content em {
    font-size: .6em;
}
.content .md-icon {
    transform: scale(1.25);
}
@media screen and (min-width: 900px) {
.content {
    margin: 3em auto;
    padding: 2em;
}
.content.list div {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
}
.content.list section {
      width: 50%;
}
}
@media screen and (min-width: 1200px) {
.content {
    padding: 1.75em;
    padding-top: 3em;
}
.content::before, .content::after {
      top: 1.25em;
}
.content::after {
      border-width: 2px;
}
.content h3 + em {
      margin-top: 0;
}
.content h2 span {
      margin-left: .5em;
}
.content.list section {
      width: 33.33333%;
}
.content .md-icon {
      transform: scale(1.5);
}
}



